import axiosInstance from "@/api/axios/axiosInstance";

export const getAllStudyParticipants = async () => {
  try {
    const response = await axiosInstance.get("/api/study-participants");
    return response.data;
  } catch (error) {
    console.error("Error fetching study participants:", error);
    throw error;
  }
};

export const getStudyParticipantById = async (id: string) => {
  try {
    const response = await axiosInstance.get(`/api/study-participants/${id}`);
    return response.data;
  } catch (error) {
    console.error("Error fetching study participant:", error);
    throw error;
  }
};

export const enrollParticipant = async (participantData: any) => {
  try {
    const response = await axiosInstance.post("/api/study-participants", participantData);
    return response.data;
  } catch (error) {
    console.error("Error enrolling participant:", error);
    throw error;
  }
};

export const updateStudyParticipant = async (id: string, participantData: any) => {
  try {
    const response = await axiosInstance.put(`/api/study-participants/${id}`, participantData);
    return response.data;
  } catch (error) {
    console.error("Error updating study participant:", error);
    throw error;
  }
};

export const removeStudyParticipant = async (id: string) => {
  try {
    const response = await axiosInstance.delete(`/api/study-participants/${id}`);
    return response.data;
  } catch (error) {
    console.error("Error removing study participant:", error);
    throw error;
  }
};

